@import url(https://use.typekit.net/zae6syr.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ux-port {
  background-color: #FAF7F6 !important;
  font-family: futura-pt, sans-serif;
  font-weight: 700;
}

.ux-port .h1, .ux-port h1, .ux-port .h2, .ux-port h2, .ux-port .h5, .ux-port h5 {
  font-weight: 700;
}

.ux-port .h2, .ux-port h2 {
  font-size: 2.5rem;
}

.ux-port .h5, .ux-port h5 {
  font-size: 1.25rem;
}

.ux-port .nav-item .btn {
  border: 3px #333333 solid;
}

.ux-port .nav-item a {
  color: #333333;
  padding-right: 1rem;
  padding-left: 1rem;
  font-size: 1.2rem;
}

.ux-port .btn {
  font-weight: bolder;
  border-radius: 17px;
  padding: 10px 20px;
}

.ux-port .btn-primary {
  background-color: #FDDD63;
  border-color: #FDDD63;
  color: #333333;
}

.ux-port .btn-secondary {
  background-color: #333333;
  border-color: #333333;
}

.ux-port .form-control, .ux-port .custom-select {
  font-weight: bolder;
  border-radius: 17px;
  border: 2px #333333 solid;
}

.ux-port .form-control {
  padding: 20px;
  margin-bottom: 0 !important;
}

.ux-port .custom-select {
  height: calc(1.5em + 1rem + 5px);
}

.ux-port .jumbotron {
  background: none;
}

.ux-port .jumbotron .hpbg {
  max-width: 100%;
}

.ux-port .jumbotron .btn-hp {
  background-color: #333333;
  border-color: #333333;
  color: #FDDD63;
}
/*# sourceMappingURL=ux-port.css.map */
